.preloader-svg {
  width: 100%;
  height: 100%;
}

.preloader {
  width: 102px;
  height: 102px;
  min-height: 102px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
}

.preloader .small-circle {
  stroke-dasharray: 210;
  stroke-dashoffset: 210px;
  transform-origin: 50%;
  animation: draw-small 1s infinite alternate;
}

@keyframes draw-small {
  0% {
    stroke-dashoffset: 0;
    transform: rotate(0deg);
  }

  100% {
    stroke-dashoffset: 210px;
    transform: rotate(360deg);
  }
}

.preloader .big-circle {
  stroke-dasharray: 240;
  stroke-dashoffset: 240px;
  transform-origin: 50%;
  animation: draw-big 1s .5s infinite alternate;
}

@keyframes draw-big {
  0% {
    stroke-dashoffset: 0;
    transform: rotateY(180deg)rotate(360deg);
  }

  100% {
    stroke-dashoffset: 240px;
    transform: rotateY(180deg)rotate(0deg);
  }
}

